@import 'node_modules/antd/lib/style/themes/default.less';
.row {
    margin-top: 10px;
    position: relative;
}

.btn {
    display: flex;
    align-items: center;
}

.label {
    text-align: right;
    line-height: 30px;
    &::after {
        display: inline-block;
        content: ":";
    }
}

.toolbar {
    margin-bottom: 10px;
}

.prefix {
    color: @select-multiple-item-disabled-color;
}

.icon {
    cursor: move;
    display: block;
    width: 20px;
    height: 20px;
    position: absolute;
    left: -18px;
    top: 5px;
    color: #AAA;
    svg {
        display: block;
        fill: currentColor;
    }
}

.item {
    position: relative;
}

:global {
    body > .ant-row.arrayarguments{
        z-index: 2000;
    }
}
