@import 'node_modules/antd/lib/style/themes/default.less';
.recordstr {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.recorditem {
    display: flex;
    margin-bottom: 1rem;
    text-align: left;
    align-items: center;
}

.recordstr {
    flex-wrap: wrap;
    text-align: left;
    flex:auto;
    >div:first-child {
        width: 100%;
    }
}

.recordwrap {
    margin: 0;
    padding: 5%;
}

.prizeimg {
    width: 4rem;
    height: 4rem;
    background-color: #fff;
    border-radius: 8rem;
    text-align: center;
    vertical-align: middle;
    flex-shrink: 0;
    overflow: hidden;
    margin-right: 0.5rem;
    img {
        width: 100%;
    }
}

.prizename {
    font-size: 1rem;
}

.receiveraddress, .wintime {
    font-size: 0.8rem;
}


.timeandbutton {
    display: flex;
    justify-content: space-between;
}

.rule {
    font-size: 1rem;
    text-align: left;
    text-align: left;
    box-sizing: border-box;
    width: 100%;
    margin: 0;
    padding: 1rem 1rem 1rem 2.2rem;
    max-height: 60vh;
    overflow: auto;
    font-size: 1rem;
    li {
        text-align: left;
        margin-bottom: 0.5rem;
    }
}
@primary-color: #2626bc;@link-color: #2626bc;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);