@import 'node_modules/antd/lib/style/themes/default.less';
.tab {
    margin-top: 20px;
}
.card {
    width: 200px;
    margin-right: 40px;
    margin-bottom: 40px;
}
.projectcove {
    width: 100px;
    height: 180px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    font-size: 30px;
    color: #2626bc;
    overflow: hidden;

    img {
        width: 100px;
        height: auto;
    }
}

.tpltitle {
    margin: 10px 0 0 0;
    line-height: 1;
}

.tpldescript {
    font-size: smaller;
    font-weight: normal;
    color: #aaa;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.projectcovetpl {
    background-color: #ddd;
    width: 100%;
    img {
        width: 100%;
    }
}

.container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.buttonbar {
    text-align: center;
}

.tag {
    padding-bottom: 10px;
}
@primary-color: #2626bc;@link-color: #2626bc;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);