@import 'node_modules/antd/lib/style/themes/default.less';
.listwrap {
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #fff;
    > li {
        display: flex;
        .cover {
            background-color: #f00;
            width: 200px;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .item {
            text-align: left;
            display: flex;
            flex-wrap: wrap;
            line-height: 1;
            flex: auto;
        }
        .itemtitle {
            width: 100%;
            flex-shrink: 0;
            padding: 0;
            margin: 0;
        }

        .itemsubtitle {
            width: 100%;
            flex-shrink: 0;
            padding: 0;
            margin: 0;
        }
        .iteminfo {
            width: 100%;
            flex-shrink: 0;
            padding: 0;
            margin: 0;
        }
        .buttons {
            text-align: right;
            width: 100%;
        }
    }

    .nodate {
        justify-content: center;
        padding-top: 20%;
    }
}

.articlelist {
    height: 100%;
}

.articlelistwrap {
    display: flex;
    flex-direction: column;
    width: 80vw;
    max-width: 400px;
    height: 80vh;
    >div{
        flex: 1;
        height: 100%;
    }
}


@primary-color: #2626bc;@link-color: #2626bc;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);