@import 'node_modules/antd/lib/style/themes/default.less';
.imgwrap {
    img {
        width: 100%;
        height: 100%;
    }
    background-color: #eee;
    width: 100%;
}

.slider {
    :global {
        .awssld__content {
            background-color: transparent;
        }
    }
}

.swipercontainer {
    width: 100%;
    height: 100%;
}

.swiperslide {
    img {
        width: 100%;
    }
}

.next, .prev {
    position: absolute;
    top: 0;
    z-index: 10;
    height: 100%;
    line-height: 100%;
    display: flex;
    width: 30px;
    align-items: center;
    justify-content: center; 
    color: #fff;
    svg {
        display: block;
        fill: currentColor;
        width: 100%;
        height: 100%;
    }
}

.next {
    right: 0;
}

.sliderWrap {
    overflow: hidden;
}



