@import 'node_modules/antd/lib/style/themes/default.less';

.color {
    width: 25px;
    height: 25px;
    border-radius: 2px;
    position: relative;
}

.swatch {
    padding: 2px;
    background: #fff;
    border-radius: 1px;
    box-shadow: 0 0 0 1px rgba(0,0,0,.1);
    display: inline-block;
    cursor: pointer;
    vertical-align: middle;
}

.popover {
    position: absolute;
    z-index: 3;
}

.cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.row {
    align-items: center;

    .label {
        text-align: right;
    }
}

.empty {
    text-align: center;
    vertical-align: middle;
    font-size: 20px;
    color: gray;
    line-height: 20px;
}

.inlineWrap {
    display: inline-block;
}

.wrap {
    position: absolute;
    box-shadow: rgb(0 0 0 / 15%) 0px 0px 0px 1px, rgb(0 0 0 / 15%) 0px 8px 16px;
    border-radius: 4px;
    background-color: #fff;
}

.picker {
    box-shadow: none !important;
}

.inherit {
    text-align: center;
    height: 40px;
    line-height: 40px;
    border-top: 1px solid rgb(235, 235, 235);
}

@primary-color: #2626bc;@link-color: #2626bc;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);